<template>

    <div class="survey-container">
        <img src="../assets/bla.svg" alt="" srcset="" style="width: 250px;">
        <span v-if="!isSubmitted">We're sorry to see you go. Help us improve by submitting this survey.</span>
        <form v-if="!isSubmitted" @submit="submit" target="_blank" method="post">
            <div class="selector-container">
                <label for="main-reason">Why are you uninstalling PatchMe?</label>
                <select class="selector" name="mainReason" id="main-reason" v-model="selectedMainReason" required>
                    <option value="">--Please choose an option--</option>
                    <option v-for="reason in reasonsArray" :key="reason" :value="reason"> {{ reason }} </option>
                </select>

            </div>
            <span>Are there any other factors that affected your decision?</span>
            <div class="other-reasons-container">
                <div v-for="reason in reducedReasons" :key="reason" class="checkbox-container">
                    <input type="checkbox" :id="reason " :name="reason" value="yes" />
                    <label :for="reason"> {{ reason }} </label>
                </div>
            </div>
            <input type="hidden" name="_captcha" value="false">
            <span>Anything else you want to mention? (Optional)</span>
            <div class="text-container">
                <textarea name="additional-feedback" id="additional-feedback" cols="60" rows="8"></textarea>
            </div>
            <div class="button-container">
                <button :disabled="buttonDisabled"  class="button-tutorial">Submit</button>
            </div>
        </form>
        <div class="thanks-feedback">
            <div v-if="isSubmitted">Thank you for your feedback.</div>
            <div v-if="isSubmitted">We hope to see you again!</div>
        </div>
    </div>

</template>

<script setup>
import { computed, ref } from 'vue';

const selectedMainReason = ref('')
const isSubmitted = ref(false)

const buttonDisabled = computed(()=> selectedMainReason.value ? false : true)
const reasonsArray = [
 'Confusing user interface',
 'Lack of features',
 'I am not interested in patch notes',
 "I stopped playing League of Legends",
 "I installed the app by mistake",
 "I installed another application with similiar but better features"
]

const reducedReasons = computed(()=> reasonsArray.filter((el)=> el != selectedMainReason.value))

// would you be open to reinstalling the application if the reasons were addressed? 

const submit = (event)=>{
    event.preventDefault()
    console.log(JSON.stringify(Object.fromEntries(new FormData(event.currentTarget))))
    fetch('https://formsubmit.co/f1a5f68a69e8ec2bf1be03bb1d3f9c82', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json'
        },
        body: JSON.stringify(Object.fromEntries(new FormData(event.currentTarget))),
    })
    console.log('sent')
    isSubmitted.value = true
}


</script>

<style scoped>
.survey-container{
    width: 70%;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
    padding-top: 1rem;
}
.survey-container span{
    font-size: large;
    min-width: 600px;
    padding-bottom: 1.4rem;
}
.selector-container{
    font-size: large;
    display: flex;
    flex-direction: column;
    gap: 0.4rem;
    padding-bottom: 2rem;
}
form{
    min-width: 600px;
}
.selector{
    padding: 0.4rem;
    background-color: var(--main-app-secondary-color);
    color: var(--main-app-white-text-color);
    border-radius: 0.3rem;
}
.other-reasons-container{
    font-size: large;
    display: flex;
    flex-direction: column;
    gap: 0.4rem;
    padding-bottom: 2rem;
    padding-top: 1.0rem;
}
.checkbox-container{
    font-size: large;
    display: flex;
    gap: 0.3rem;
}
input[type = 'checkbox']{
    width: 1rem;
}

.button-container{
    display: flex;
    width: 100%;
    justify-content: center;
    padding: 15px 0px;
}
.button-tutorial{
    padding: 10px;
    border: solid var(--main-app-cta-color) 2px;
    color: var(--main-app-white-text-color);
    background-color: var(--main-app-cta-color);
    border-radius: 5px;
    width: 150px;
    font-size: large;
}
.button-tutorial:focus{
    outline: none;
}
.button-tutorial:hover{
    filter: brightness(1.1);
    cursor: pointer;
}

.button-tutorial:disabled{
    background-color: var(--main-app-bg-color);
    filter: brightness(0.5);
}
.button-tutorial:disabled:hover{
    filter: brightness(0.5);
    cursor: default;
}
textarea{
    background-color: var(--main-app-secondary-color);
    color: var(--main-app-white-text-color);
    padding: 1rem;
    box-sizing: border-box;
    font-size: larger;
    margin-top: 0.8rem;
}
textarea:focus{
    outline: none;
}
.thanks-feedback{
    font-size: large;
    padding-top: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
}
</style>
